@import '../../constants/index.scss';

.tab3 {
  &__controller {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }

  &__item {
    gap: 4px;
    height: 44px;
    display: flex;
    align-items: center;
    color: $color-primary-text;
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    padding-left: 12.75px;
    min-width: 50%;

    &.active {
      position: relative;
      color: $color-primary;

      &::before {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: $color-primary;
      }
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 28px;
    margin: 0 0 12px 0;
  }
}